section#features .section-image-container .feature-info {
  margin-top: 2rem;
  color: #fff;
  font-weight: 700;
}
section#features .section-image-container .image-wrapper {
  width: 0;
  height: 0;
}
section#features .section-image-container .image-wrapper img {
  transform: translateY(10%);
  opacity: 0;
  transition: transform 0.8s ease, opacity 0.6s ease;
}
section#features .section-image-container .image-wrapper .feature-info {
  opacity: 0;
  transition: opacity 1s ease;
}
section#features .section-image-container .image-wrapper.active {
  width: 100%;
  height: auto;
}
section#features .section-image-container .image-wrapper.active img {
  transform: translateY(0);
  opacity: 1;
}
section#features .section-image-container .image-wrapper.active .feature-info {
  opacity: 1;
}
@media (min-width: 992px) {
  section#features .section-text-container {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
section#features .features-wrapper {
  border-left: 1px solid rgba(0, 0, 0, 0.45);
  padding-bottom: 1.25rem;
}
section#features .features-wrapper .btn,
section#features .features-wrapper .sl-no {
  font-size: calc(1.2625rem + 0.15vw);
  font-weight: 700;
  color: rgba(42, 45, 51, 0.5);
  white-space: nowrap;
}
@media (min-width: 1200px) {
  section#features .features-wrapper .btn,
section#features .features-wrapper .sl-no {
    font-size: 1.375rem;
  }
}
@media (min-width: 1400px) {
  section#features .features-wrapper .btn,
section#features .features-wrapper .sl-no {
    font-size: 1.5rem;
  }
}
section#features .features-wrapper .sl-no {
  margin-right: 0.5rem;
}
section#features .features-wrapper .btn {
  display: block;
  padding: 1.25rem 2.5rem;
  text-align: left;
}
section#features .features-wrapper .btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -0.25rem;
  display: inline-block;
  width: 0.25rem;
  height: 0;
  border-radius: 1rem;
  background-color: transparent;
  transition: height 0.3s ease-in-out;
}
