section#features .features-wrapper .btn.active::before {
  height: 100%;
  background-color: #1E3963;
}
section#features .features-wrapper .btn.active .sl-no {
  color: #1E3963;
}
section#pricing .pricing-card-container .pricing-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #2A2D33;
}
section#pricing .pricing-card-container .pricing-description {
  font-size: 0.875rem;
  color: #7C828D;
}
section#pricing .pricing-card-container .pricing-card-wrapper {
  padding: 0.625rem 1.25rem;
}
@media (min-width: 992px) {
  section#pricing .pricing-card-container .pricing-card-wrapper {
    padding: 0 1.25rem;
  }
}
section#pricing .pricing-card-container .pricing-card {
  padding: 2rem;
  box-shadow: 0 0.625rem 1.875rem rgba(172, 171, 171, 0.6);
  border-radius: 0.625rem;
}
@media (min-width: 768px) {
  section#pricing .pricing-card-container .pricing-card {
    max-width: 21.5rem;
  }
}
section#pricing .pricing-card-container .pricing-features {
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  min-height: 17.125rem;
  /*To match the height of the cards*/
  text-align: left;
}
section#pricing .pricing-card-container .pricing-features li {
  font-size: 0.875rem;
  color: #7C828D;
  margin-bottom: 0.75rem;
}
section#pricing .pricing-card-container .pricing-features li::before {
  float: left;
  display: inline-block;
  content: "";
  width: 1rem;
  height: 1rem;
  margin-right: 0.625rem;
  background-size: contain;
  vertical-align: middle;
}
section#pricing .pricing-card-container .pricing-features li span {
  display: block;
  overflow: hidden;
}
section#pricing .pricing-card-container .pricing-features li:not(.inactive) {
  background: transparent url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='8' cy='8' r='8' fill='%231E3963'/%3E%3Cpath d='M12.67 4.87623L6.03915 12.3335L3 8.91558L3.77913 8.03935L6.03915 10.5748L11.8909 4L12.67 4.87623Z' fill='white'/%3E%3C/svg%3E%0A") left center no-repeat;
}
section#pricing .pricing-card-container .pricing-features li.inactive {
  background: transparent url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='8' cy='8' r='8' fill='%23000000' fill-opacity='0.26'/%3E%3Cpath d='M12.67 4.87623L6.03915 12.3335L3 8.91558L3.77913 8.03935L6.03915 10.5748L11.8909 4L12.67 4.87623Z' fill='white'/%3E%3C/svg%3E%0A") left center no-repeat;
}
section#pricing .pricing-card-container .price-wrapper {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}
section#pricing .pricing-card-container .price-text {
  font-size: 3rem;
  font-weight: 700;
  color: #2A2D33;
}
section#pricing .pricing-card-container .price-user-text {
  font-size: 1.125rem;
  color: #7C828D;
}
section#pricing .pricing-card-container .cta-btn-wrapper {
  margin-top: 2rem;
  margin-bottom: 1.5rem;
}
section#pricing .pricing-card-container .cta-btn {
  background-color: #011842;
  box-shadow: 0 0.625rem 1.5rem 0 #7B68EE80;
}
section#pricing .pricing-card-container .help-info {
  color: #7C828D;
}
section#faqs .accordion-button,
section#faqs .accordion-body {
  font-size: 1.25rem;
}
section#faqs .accordion-button {
  color: #2A2D33;
  box-shadow: none;
}
section#faqs .accordion-button:focus {
  box-shadow: none;
}
section#faqs .accordion-button::before {
  flex-shrink: 0;
  width: 1.25rem;
  height: 1.25rem;
  content: "";
  margin-right: 0.5rem;
  border-radius: 2rem;
  background-color: #1E3963;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 7.5H7.5V12H4.5V7.5H0V4.5H4.5V0H7.5V4.5H12V7.5Z' fill='white'/%3E%3C/svg%3E");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 0.75rem;
  transition: transform 0.2s ease-in-out;
}
section#faqs .accordion-button::after {
  display: none;
}
section#faqs .accordion-button:not(.collapsed) {
  color: inherit;
  background-color: transparent;
  box-shadow: none;
}
section#faqs .accordion-button:not(.collapsed)::before {
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='4' viewBox='0 0 12 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 4H0V0H12V4Z' fill='white'/%3E%3C/svg%3E");
}
section#faqs .accordion-body {
  max-width: 40rem;
  /*width of the longest content - fix for long answer makes wider when open;*/
  padding: 0 0 2rem 3rem;
  color: rgba(0, 0, 0, 0.66);
}

footer {
  margin-top: auto;
  /*keep the footer always at the bottom*/
  background-color: #fff;
  box-shadow: 0 -0.063rem 0.25rem rgba(0, 0, 0, 0.25);
}
footer .footer-inner-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-right: 2.25rem;
  padding-left: 2.25rem;
  min-height: 9rem;
}
footer .footer-inner-container .footer-logo-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 3rem;
}
footer .footer-inner-container .footer-logo-wrapper .footer-logo {
  width: auto;
  height: 4.5rem;
}
footer .footer-inner-container .nav-item .nav-link,
footer .footer-inner-container .footer-country {
  padding: 0 0.5rem;
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  font-size: 0.75rem;
  line-height: 1.2;
}
footer .footer-inner-container .nav {
  justify-content: center;
}
footer .footer-inner-container .nav .nav-item .nav-link {
  color: rgba(0, 0, 0, 0.66);
  border-left: 1px solid rgba(0, 0, 0, 0.44);
}
footer .footer-inner-container .nav .nav-item:first-child .nav-link {
  border: none;
}
footer .footer-inner-container .footer-country {
  margin-top: 0.5rem;
}

@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0 rgba(151, 11, 11, 0.4);
  }
  100% {
    box-shadow: 0 0 0 0.5rem rgba(151, 11, 11, 0);
  }
}